.conversation{
    width: 100%;
    height: 70px;
    padding: 5px 10px;
    border-bottom: 1px solid  rgb(63, 101, 134);

    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    flex-wrap: nowrap;
    
    cursor: pointer;
}
.conversation:hover{
    background-color: rgba(240, 248, 255, 0.066);
}
.profilePic{
    width: 45px;
    height: 45px;
    border-radius: 22.5px;
}
.lastMessage{
    opacity: 0.675;
    font-size: small;
    padding-left: 1px;
}.userDetailsDiv {
    white-space: nowrap; /* Prevent text wrapping */
    overflow: hidden; /* Hide overflowing text */
    text-overflow: ellipsis; /* Show "..." for truncated text */
}
.mails{
    opacity: 0.575;
}
.conversation {
    padding: 10px;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
}

.conversation.selected {
    background-color: #ffffff12; /* Change this to the desired color */
}
.conversation.selected:hover{
    background-color: #ffffff12; /* Change this to the desired color */

}
