/* Login.css */

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    background: linear-gradient(135deg, #6e7dff, #4ac29c);
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.card {
    background-color: #fff;
    border-radius: 12px;
    padding: 40px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    width: 400px;
    transition: transform 0.3s ease-in-out;
}

.heading {
    text-align: center;
    font-size: 32px;
    margin-bottom: 20px;
    color: #333;
}

.error {
    color: red;
    text-align: center;
    margin-bottom: 15px;
}

.form {
    display: flex;
    flex-direction: column;
}

.inputGroup {
    margin-bottom: 20px;
}

.label {
    font-size: 14px;
    color: #555;
    margin-bottom: 6px;
}

.input {
    padding: 12px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 8px;
    outline: none;
    transition: border-color 0.3s;
}

.input:focus {
    border-color: #6e7dff;
}

.button {
    padding: 14px;
    font-size: 18px;
    background-color: #6e7dff;
    color: #fff;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
}
#already {
    color: #555;
}
.showHidePassword {
    background: none;
    border: none;
    color: #007bff;
    cursor: pointer;
    font-size: 14px;
    padding: 0;
    margin-left: 8px;
}
.inputGroup {
    position: relative;
    margin-bottom: 20px;
}

.passwordWrapper {
    position: relative;
}

.passwordInput {
    width: 100%;
    padding-right: 40px; /* Space for the button inside the input */
}

.showHidePassword {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: #007bff;
    cursor: pointer;
    font-size: 14px;
}

.input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.button:disabled {
    background-color: #ddd;
}

.error {
    color: red;
    font-size: 14px;
}

@media (max-width: 768px) {
    .card {
        width: 90vw;
        max-width: 90vw;
    }
}
